import React from 'react'
import Typography from "@material-ui/core/Typography";
import Typical from "react-typical";
import {AwesomeButton} from "react-awesome-button";
import {Parallax, ParallaxLayer} from "react-spring/renderprops-addons";
import me from "../Img/Me.jpeg";
import logo from "../Img/logo.svg";
import javaScriptIcon from "../Img/javaScriptLogo.png";
import html from "../Img/html.png";
import css from "../Img/cssLogo.png";




class IntroPage extends React.Component{


    render() {
        return (
            <Parallax ref={ref => (this.parallax = ref)} pages={2}>
                <div id={'introPage'} className={'body'} style={{position: 'relative', height: '150vh'}}>
                    <Typography className={'text'} style={{fontSize: '12.5vh'}}>
                        <Typical
                            steps={[750, 'Welcome', 1500, '', 750, 'ようこそ', 1500,'', 750, '어서 오십시오', 1500, '',]}
                            loop={Infinity}
                            wrapper="p"
                        />
                    </Typography>
                    <AwesomeButton className={'button'}
                                   type={'secondary'}
                                   size={'large'}
                                   style={{position: 'absolute', left: 0, right: 0, margin: 'auto', bottom: '80vh', top: '35vh', height: '6vh'}}
                                   onPress={() => this.parallax.scrollTo( 1)}
                    >
                        Learn about me!
                    </AwesomeButton>

                    <ParallaxLayer offset={1} speed={.2} factor={1} style={{ backgroundColor: 'black' }}/>

                    <ParallaxLayer offset={1.23} speed={.3}>
                        <Typography style={{color: 'white', marginLeft: '50%', top: '25%', fontSize: '5vh'}}>
                            Welcome to my Website!
                        </Typography>


                        <Typography style={{color: 'white', marginLeft: '50%', top: '30%',marginRight: '10%', fontSize: '2.5vh'}}>
                            Hi! My name is Michael Ewers and this is my personal website. The majority of information and functionality is
                            currently under construction! These web pages have not fully been adapted to mobile browsers. MOBILE USERS BEWARE.
                            Kindly check back another time to see updates!
                        </Typography>

                    </ParallaxLayer>

                    <ParallaxLayer offset={1.25} speed={.4}>
                        <img src={me} style={{display: 'block',marginLeft: '20%', width: '25%'}}/>
                    </ParallaxLayer>



                    <ParallaxLayer offset={1.2} speed={.5}>
                        <img src={logo} style={{display: 'block',marginLeft: '5%', width: '10%'}}/>
                    </ParallaxLayer>

                    <ParallaxLayer offset={1.4} speed={.6}>
                        <img src={javaScriptIcon} style={{display: 'block',marginLeft: '7.5%', width: '5%'}}/>
                    </ParallaxLayer>

                    <ParallaxLayer offset={1.6} speed={.7}>
                        <img src={html} style={{display: 'block',marginLeft: '7.5%', width: '5%'}}/>
                    </ParallaxLayer>

                    <ParallaxLayer offset={1.8} speed={.8}>
                        <img src={css} style={{display: 'block',marginLeft: '7.5%', width: '5%'}}/>


                    </ParallaxLayer>
                </div>
            </Parallax>
        );
    }


}
export default IntroPage;