import React from 'react'
import {AppBar, MenuItem, Toolbar} from "@material-ui/core";
import {Link} from "react-router-dom";




class ApplicationBar extends React.Component{
    constructor(props) {
        super(props);
        this.state = {MenuOpen : false}

    }



    render(){
        return(
            <AppBar position={"fixed"} style={{background: '#f5d142'}}>


                <Toolbar>
                    <Link to={'/'} style={{textDecoration: "none"}} ><MenuItem style={{color: 'black'}}>Home</MenuItem></Link>

                    <section style={{marginLeft: 'auto', marginRight: -12}}>
                        <Toolbar>
                            <Link style={{textDecoration: "none"}} to={'/interests'}><MenuItem style={{color: 'black'}}>Interests</MenuItem></Link>
                            <a style={{textDecoration: "none"}} href={'https://www.linkedin.com/in/mewersdev/'}><MenuItem style={{color: 'black'}}>LinkedIn</MenuItem></a>
                            <a style={{textDecoration: "none"}} href={'https://github.com/MEwers96'}><MenuItem style={{color: 'black'}}>Github</MenuItem></a>
                        </Toolbar>
                    </section>
                </Toolbar>
            </AppBar>
        )
    }

}
export default ApplicationBar